@import './variables';
@import './normalize';
@import './pages/all';
@import './components/all';

body {
    margin: 0;
    padding: 0;
  }
  
  *, *:after, *:before {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    margin: 0;
  }
  
  .App {
    overflow: auto;
    width: 100%;
    min-height: 100vh;
    min-width: 1250px;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: $bgColorPrimary;
    color: $fontColorPrimary;
  
    .Header {
      min-width: 1230px;
      height: 200px;
      display: flex;
      justify-content: space-between;
      align-content: center;
      padding-bottom: 30px;
      padding-top: 30px;
  
      .TopCap {
        display: flex;
        flex-direction: row;
        width: 40%;
        height: 100px;
        align-items: center;
        margin-left: 5rem;
  
        .Icon{
          height: 5rem;
          width: 5rem;
          margin: .5rem .5rem .5rem .5rem;
        }
  
        .MVL{
            color: $fontColorPrimary;
            font-size: 1.25rem;
        }
      }
    }
  
      .NavBar{
        width: 50%;
        
        .Menu{
          list-style-type: none;
          overflow: hidden;
          display: block;
          margin: 0 auto;
          height: 200px;
          padding: 10px 10px 0 10px;
          
          .MenuOption{
            float: left;
            width: 25%;
            cursor: pointer;
  
            p{
              display: block;
              padding: 10px;
              margin: 0 5px 0 5px;
              text-align: center;
              color: $fontColorPrimary;
              font-size: 20px;
              border-bottom: 1px solid white;
            }
  
            .MenuDropDown{
              background: $bgColorAccent;
              height: 0px;
              opacity: 0;
              transition: 
                .75s height ease-out,
                .5s .2s opacity;
              top: 35px;
              list-style-type: none;
              padding: 10px;
              margin: 0 5px 0 5px;
              display: flex;
              flex-direction: column;
              justify-content: space-between;
              align-items: center;
              border-bottom-right-radius: 10px;
              border-bottom-left-radius: 10px;
  
              li{
                
                a{
                  color: rgb(0,0,0,0);
                  text-decoration: none;
                  transition: .25s color;
                }
              }
            }
  
            &:hover .MenuDropDown {
              height: 120px;
              opacity: 1;
              transform: translateY(0);
  
              li{
                a{
                  color: $fontColorPrimary;
  
                  &:hover{
                    color: $fontColorActive;
                  }
                }
              }
            }
  
            a{
              text-decoration: none;
            }
          }
  
          
        }
    }
  
    .Content {
      display: flex;
      flex-direction: column;
      min-width: 1230px;
      min-height: 70vh;
      padding: 20px 0 20px 0;
    }
  
    .Footer {
      width: 100%;
      height: 80px;
      display: flex;
      justify-content: center;
      align-items: center;
      background-color: $bgColorPrimary;
      color: $fontColorPrimary;
    }
  }
  